<template>
	<div id="store-newhome">
		<public-header :title="store.title"></public-header>
		<public-footer :menufooter="menufooter"></public-footer>
		<div class="content">
			<div class="header-title flex-lr">
				<div class="title-wrap">
					<div class="font-18 font-bold">{{stat.final_fee}}</div>
					<div class="font-13 padding-5-t">今日总收入</div>
				</div>
				<div class="title-wrap">
					<div class="font-18 font-bold">{{stat.total_order}}</div>
					<div class="font-13 padding-5-t">有效订单</div>
					<span class="icon icon-right font-16"></span>
				</div>
			</div>
			<div class="nav-list">
				<van-row>
					<van-col span="6">
						<router-link tag="div" :to="util.getUrl({path: '/pages/goods/index'})">
							<div class="icon icon-goods"></div>
							<div class="font-13">商品</div>
						</router-link>
					</van-col>
					<van-col span="6">
						<router-link tag="div" :to="util.getUrl({path: '/pages/service/comment'})">
							<div class="icon icon-survey"></div>
							<div class="font-13">评论</div>
						</router-link>
					</van-col>
					<van-col span="6">
						<router-link tag="div" :to="util.getUrl({path: '/pages/finance/index'})">
							<div class="icon icon-recharge"></div>
							<div class="font-13">资产</div>
						</router-link>
					</van-col>
					<van-col span="6">
						<router-link tag="div" :to="util.getUrl({path: '/pages/shop/setting'})">
							<div class="icon icon-shop"></div>
							<div class="font-13">店铺</div>
						</router-link>
					</van-col>
				</van-row>
			</div>
			<div class="placeholder"></div>
			<div class="swipe-wrap" v-if="ads.length > 0">
				<van-swipe :autoplay="3000">
					<van-swipe-item v-for="(item, index) in ads" :key="item.id">
						<div @click="util.jsUrl(item.link)">
							<img :src="util.tomedia(item.thumb)" />
						</div>
					</van-swipe-item>
				</van-swipe>
			</div>
			<div class="make-money">
				<div class="make-money-title">赚钱技巧</div>
				<div class="skill-list">
					<div class="skill-wrap">
						<div class="skill-item flex ">
							<router-link class="item-info flex" tag="div" :to="util.getUrl({path: '/pages/statcenter/index'})" >
								<div class="img">
									<img src="http://mine/we7/attachment//images/1/2019/01/ocrsIec7NnH1DCD5Rhv8H78IUR2S8N.jpg" alt="">
								</div>
								<div class="item-info-left padding-10-l">
									<div class="font-16 font-bold">销售统计</div>
									<div class="font-14 c-gray padding-5-t">销售精准分析</div>
								</div>
							</router-link>
							<router-link tag="div" :to="util.getUrl({path: '/pages/activity/index'})" class="item-info flex">
								<div class="img">
									<img src="http://mine/we7/attachment//images/1/2019/01/ocrsIec7NnH1DCD5Rhv8H78IUR2S8N.jpg" alt="">
								</div>
								<div class="item-info-left padding-10-l">
									<div class="font-16 font-bold">钜惠活动</div>
									<div class="font-14 c-gray padding-5-t">实惠让利顾客</div>
								</div>
							</router-link>
						</div>
						<div class="skill-item flex van-hairline--top">
							<router-link tag="div" :to="util.getUrl({path: '/pages/advertise/index'})" class="item-info flex" v-if="util.checkplugin('advertise')">
								<div class="img">
									<img src="http://mine/we7/attachment//images/1/2019/01/ocrsIec7NnH1DCD5Rhv8H78IUR2S8N.jpg" alt="">
								</div>
								<div class="item-info-left padding-10-l">
									<div class="font-16 font-bold">店铺推广</div>
									<div class="font-14 c-gray padding-5-t">获取更多顾客</div>
								</div>
							</router-link>
							<router-link tag="div" :to="util.getUrl({path: '/pages/service/comment'})" class="item-info flex">
								<div class="img">
									<img src="http://mine/we7/attachment//images/1/2019/01/ocrsIec7NnH1DCD5Rhv8H78IUR2S8N.jpg" alt="">
								</div>
								<div class="item-info-left padding-10-l">
									<div class="font-16 font-bold">优质售后</div>
									<div class="font-14 c-gray padding-5-t">评价提升服务</div>
								</div>
							</router-link>
						</div>
					</div>
				</div>
			</div>
			<div class="tool-wrap">
				<div class="tool-title van-hairline--bottom">实用工具</div>
				<van-row>
					<van-col span="6">
						<router-link tag="div" :to="util.getUrl({path: '/pages/shop/setting'})">
							<div class="icon icon-shop c-danger"></div>
							<div class="font-14 padding-5-t">店铺管理</div>
						</router-link>
					</van-col>
					<van-col span="6">
						<router-link tag="div" :to="util.getUrl({path: '/pages/order/index'})">
							<div class="icon icon-order c-warning"></div>
							<div class="font-14 padding-5-t">外卖订单</div>
						</router-link>
					</van-col>
					<van-col span="6">
						<router-link tag="div" :to="util.getUrl({path: '/pages/order/tangshi/index'})">
							<div class="icon icon-meal c-info"></div>
							<div class="font-14 padding-5-t">店内订单</div>
						</router-link>
					</van-col>
					<van-col span="6">
						<router-link tag="div" :to="util.getUrl({path: '/pages/goods/index'})">
							<div class="icon icon-goods c-warning"></div>
							<div class="font-14 padding-5-t">商品管理</div>
						</router-link>
					</van-col>
					<van-col span="6">
						<router-link tag="div" :to="util.getUrl({path: '/pages/service/comment'})">
							<div class="icon icon-survey c-info"></div>
							<div class="font-14 padding-5-t">评论管理</div>
						</router-link>
					</van-col>
					<van-col span="6">
						<router-link tag="div" :to="util.getUrl({path: '/pages/finance/index'})">
							<div class="icon icon-recharge c-danger"></div>
							<div class="font-14 padding-5-t">资产</div>
						</router-link>
					</van-col>
					<van-col span="6">
						<router-link tag="div" :to="util.getUrl({path: '/pages/activity/index'})">
							<div class="icon icon-activity c-warning"></div>
							<div class="font-14 padding-5-t">店铺活动</div>
						</router-link>
					</van-col>
					<van-col span="6" v-if="util.checkplugin('advertise')">
						<router-link tag="div" :to="util.getUrl({path: '/pages/advertise/index'})">
							<div class="icon icon-medal c-info"></div>
							<div class="font-14 padding-5-t">店铺推广</div>
						</router-link>
					</van-col>
					<van-col span="6">
						<router-link tag="div" :to="util.getUrl({path: '/pages/news/notice'})">
							<div class="icon icon-newshot c-danger"></div>
							<div class="font-14 padding-5-t">公告</div>
						</router-link>
					</van-col>
					<van-col span="6">
						<router-link tag="div" :to="util.getUrl({path: '/pages/paybill/index'})">
							<div class="icon icon-signboard c-warning"></div>
							<div class="font-14 padding-5-t">买单</div>
						</router-link>
					</van-col>
					<van-col span="6">
						<router-link tag="div" :to="util.getUrl({path: '/pages/order/table'})">
							<div class="icon icon-similar c-info"></div>
							<div class="font-14 padding-5-t">桌台管理</div>
						</router-link>
					</van-col>
					<van-col span="6">
						<router-link tag="div" :to="util.getUrl({path: '/pages/order/assign'})">
							<div class="icon icon-friend_light c-danger"></div>
							<div class="font-14 padding-5-t">排队</div>
						</router-link>
					</van-col>
					<van-col span="6" v-if="util.checkplugin('gohome')">
						<router-link tag="div" :to="util.getUrl({path: '/pages/gohome/index'})">
							<div class="icon icon-home c-warning"></div>
							<div class="font-14 padding-5-t">生活圈</div>
						</router-link>
					</van-col>
					<van-col span="6">
						<router-link tag="div" :to="util.getUrl({path: '/pages/statcenter/index'})">
							<div class="icon icon-rank c-info"></div>
							<div class="font-14 padding-5-t">商户统计</div>
						</router-link>
					</van-col>
					<van-col span="6">
						<router-link tag="div" :to="util.getUrl({path: '/pages/statcenter/index'})">
							<div class="icon icon-print c-warning"></div>
							<div class="font-14 padding-5-t">打印机</div>
						</router-link>
					</van-col>
				</van-row>
			</div>
			<div class="article-list" v-if="news.length > 0">
				<div class="article-head flex-lr van-hairline--bottom">
					<div class="title">商家学园</div>
					<router-link :to="util.getUrl({path: '/pages/news/news/index'})" class="c-gray font-14">更多<span class="icon icon-right padding-5-l"></span></router-link>
				</div>
				<div class="article-item" v-for="(item, index) in news" :key="item.id">
					<router-link :to="util.getUrl({path: '/pages/news/news/detail', query:{id: item.id}})">
						<div class="article-wrap flex-lr">
							<div class="article-left">
								<div class="text">{{item.title}}</div>
								<div class="read">阅读 <span>{{item.click}}</span></div>
							</div>
							<div class="article-img">
								<img :src="item.thumb" alt="">
							</div>
						</div>
					</router-link>
				</div>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import publicFooter from "@/components/footer"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				store: {},
				stat: {},
				news: {},
				ads: {},
				menufooter: {},
				showPreLoading: true,
			}
		},
		components: {
			publicHeader,
			publicFooter
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: this,
					url: 'manage/shop/index/newindex',
					data: {
						_account_perm: 1,
						menufooter: 1,
					},
					success: (res) => {
						that.store = res.store;
						that.stat = res.stat;
						that.news = res.news;
						if (res.ads){
							that.ads = res.ads;
						}
					}
				});
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#store-newhome .header-title{
		padding: 20px 15px 70px 15px;
		background-color: #33aafc;
		width: 100%;
		color: #fff;
	}
	#store-newhome .header-title .title-wrap{
		position: relative;
		width: 50%;
		text-align: center;
	}
	#store-newhome .header-title .title-wrap div:last-child{
		opacity: 0.8;
	}
	#store-newhome .header-title .title-wrap:last-child span{
		position: absolute;
		right: 0;
		top: 0;
		bottom: 0;
		margin-top: 9px;
	}
	#store-newhome .nav-list{
		position: absolute;
		top: 76px;
		left: 15px;
		right: 15px;
		padding: 15px;
		border-radius: 5px;
		background-color: #fff;
		box-shadow: 1px 2px 8px rgba(0,0,0,0.06);
	}
	#store-newhome .nav-list .van-col{
		text-align: center;
	}
	#store-newhome .nav-list .van-col .icon{
		color: #33aafc;
		font-size: 24px;
		padding-bottom: 5px;
	}
	#store-newhome .placeholder{
		width: 100%;
		height: 32px;
		background: #fff;
	}
	#store-newhome .swipe-wrap{
		padding: 10px 15px;
		background: #fff;
	}
	#store-newhome .swipe-wrap img{
		width: 100%;
		height: 84px;
	}
	#store-newhome .make-money{
		margin-top: 10px;
		background-color: #fff;
	}
	#store-newhome .make-money .make-money-title{
		padding: 10px 15px;
		font-size: 16px;
		font-weight: bold;
	}
	#store-newhome .make-money .skill-list{
		padding: 10px 15px;
	}
	#store-newhome .make-money .skill-list .skill-wrap{
		border-radius: 5px;
		border: 1px solid #e5e5e5;
		box-shadow: 1px 2px 8px rgba(0,0,0,0.06);
	}
	#store-newhome .make-money .skill-list .skill-wrap .skill-item{
		padding: 10px 15px;
	}
	#store-newhome .make-money .skill-list .skill-wrap .skill-item .item-info{
		width: 50%;
	}
	#store-newhome .make-money .skill-list .skill-wrap .skill-item .item-info .item-info-left{
		width: 70%;
	}
	#store-newhome .make-money .skill-list .skill-wrap .skill-item .img{
		width: 40px;
		height: 40px;
	}
	#store-newhome .make-money .skill-list .skill-wrap .skill-item .img img{
		width: 100%;
		height: 100%;
		border-radius: 100%;
	}
	#store-newhome .make-money .skill-list .skill-wrap .item-active-info{
		width: 50%;
		justify-content: center;
		border-left: 1px solid #e5e5e5;
	}
	#store-newhome .tool-wrap{
		background-color: #fff;
	}
	#store-newhome .tool-wrap .tool-title{
		padding: 10px 15px;
		font-size: 16px;
		font-weight: bold;
	}
	#store-newhome .tool-wrap .van-col{
		padding: 10px 0;
		text-align: center;
	}
	#store-newhome .tool-wrap .van-col .icon{
		font-size: 24px;
	}
	#store-newhome .article-list{
		margin: 10px 0;
		background-color: #fff;
	}
	#store-newhome .article-head{
		padding: 10px 15px;
	}
	#store-newhome .article-head .title{
		font-size: 16px;
		font-weight: bold;
	}
	#store-newhome .article-item{
		padding: 10px 15px;
		color: #333;
	}
	#store-newhome .article-item .article-wrap .article-left{
		padding-right: 10px;
		flex: 1;
		word-wrap: break-word;
		color: #333;
	}
	#store-newhome .article-item .article-wrap .article-left .text{
		font-size: 16px;
		font-weight: bold;
		line-height: 22px;
		height: 44px;
	}
	#store-newhome .article-item .article-wrap .article-left .read{
		padding-top: 10px;
		font-size: 14px;
		color: #999;
	}
	#store-newhome .article-item .article-wrap .article-left .read span{
		padding-left: 3px;
	}
	#store-newhome .article-item .article-wrap .article-img{
		height: 68px;
		width: 40%;
	}
	#store-newhome .article-item .article-wrap .article-img img{
		width: 100%;
		height: 100%;
		border-radius: 3px;
	}
</style>


